import { Component, ViewChild, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProductService } from "../../providers/product.services";
import { environment} from '../../../environments/environment'
import { ImageAttribute } from 'ionic-image-loader';


@Component({
  selector: 'page-category',
  templateUrl: 'category.html',
  styleUrls: ['./category.scss'],
})
export class CategoryPage implements OnInit {


  public SubCategoryList: any=[]
  public baseUrl = environment.baseUrl + '/';
  public searchWord: any;
  public imageAttributes: ImageAttribute[] = [];
  public isDataLoaded: Boolean = false;

  constructor(
    private _ProductService: ProductService
  ) { }

  public searchproductList: any = []
  ngOnInit() {
    this.imageAttributes.push({
      element: 'style',
      value: 'height: 70px; width: 70px; margin: 0 auto;'
    })
    this.getSubCategoryList();

  }

  public showSearchBox: Boolean = false;
  enableSearch() {
    this.showSearchBox = true
  }

  backToNormal() {
    this.showSearchBox = false
  }

 public refershDefault = {
    target: {
      complete: function () {
        return true
      }
    }
  }
  getSubCategoryList(event = this.refershDefault) {
    this._ProductService.listSubCategory().subscribe((data: any) => {
      this.SubCategoryList = data.SubCategoryList;
      this.isDataLoaded = true;
      console.log(data, "--------------------------", this.SubCategoryList)

    if (event) { }
      setTimeout(() => {
        console.log('Async operation has ended');
        event.target.complete();
      }, 1500);
    });
  }

 

  public images;
  public isSearchEnable: Boolean = false;

  public _editData={
    logo:''
  }
  selectImage(value) {
    // alert("called")
    this.images = [];
    this.images = [this.selectIcon(value)];
  }

  public audiIcon = "../../../../../assets/img/brand/audi.svg";
  public bmwIcon = "../../../../../assets/img/brand/bmw.svg";
  public hondaIcon = "../../../../../assets/img/brand/honda.svg";
  public hyundaiIcon = "../../../../../assets/img/brand/hyundai.svg";
  public kiaIcon = "../../../../../assets/img/brand/kia.svg";
  public marutiIcon = "../../../../../assets/img/brand/maruti.svg";
  public tataIcon = "../../../../../assets/img/brand/tata.svg";
  public toyotaIcon = "../../../../../assets/img/brand/toyota.svg";
  public othersIcon = "../../../../../assets/img/brand/others.png";
  public nissianIcon = "../../../../../assets/img/brand/nissan.jpg";
  public renaultIcon = "../../../../../assets/img/brand/renault.svg";
  public volkswagenIcon = "../../../../../assets/img/brand/volkswagen.svg";
  public mgIcon = "../../../../../assets/img/brand/mg.png";
  public fordIcon = "../../../../../assets/img/brand/ford.svg";
  public mahindraIcon = "../../../../../assets/img/brand/mahindra.jpg";

  selectIcon(icon) {
    switch (icon) {
      case "AUDI":
        return this.audiIcon;
      case "BMW":
        return this.bmwIcon;
      case "HONDA":
        return this.hondaIcon;
      case "HYUNDAI":
        return this.hyundaiIcon;
      case "KIA":
        return this.kiaIcon;
      case "MARUTI":
        return this.marutiIcon;
      case "TATA":
        return this.tataIcon;
      case "TOYOTA":
        return this.toyotaIcon;
      case "OTHERS":
        return this.othersIcon;
      case "FORD":
        return this.fordIcon;
      case "MAHINDRA":
        return this.mahindraIcon;
      case "NISSAN":
        return this.nissianIcon;
      case "VOLKSWAGEN":
        return this.volkswagenIcon;
      case "RENAULT":
        return this.renaultIcon;
        case "MG":
          return this.mgIcon;
    }
  }
  
}

